#dashboard{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: "100%";
    height: "100vh";
    padding: 20px;
    font-family: 'Roboto', sans-serif;
}

#navbar{
    padding: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

#navbar-grp{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}


#profile-menu{
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 80vh;
    padding: 20px;
}
#profile-top{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  flex-wrap: wrap;
    align-items:center;
}

#input-data{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 10px;
}



#rewards-menu{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding:20px;
    align-items: center;
}

#coins{
    font-size: 60px;
    color:#40a9ff;
    margin:10px;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.coins-logo-gold{
   
    color: 	#FFD700;
}

#main{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

#refer-container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding:20px;
    align-items: center;
}

#redeem-container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding:20px;
    align-items: center;
}