@import url(https://fonts.googleapis.com/css2?family=Galada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
#container{
    padding:25px;
   display: flex;
   flex-direction: column;
    font-family: 'Roboto', sans-serif;
    color:"#16181e";
}

#logo{
    font-family: 'Galada', cursive;
    color:black;
    font-size: 40px;
}

#logo-middle{
    font-family: 'Galada', cursive;
    color:#40a9ff;
    font-size: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#top-nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#middle{
    padding: 50px;;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
}
#middle-btn-grp{
    float: right;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 20px;
}

#card-grp{
    padding: 30px;
    display: flex;
   
}
 #stats{
     display: flex;
     flex-direction: row;
     flex-wrap: wrap;
     justify-content: space-around;
     align-items: center;
     padding: 20px;
     
 }
#footer{
    padding: 20px;
    text-align: center;
}

#links{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 5px;
}




@media only screen and (max-width: 600px) {
    #logo-middle{
        display:none;
    }
    #card-grp{
        display: block;
    }
  }
#dashboard{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: "100%";
    height: "100vh";
    padding: 20px;
    font-family: 'Roboto', sans-serif;
}

#navbar{
    padding: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

#navbar-grp{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}


#profile-menu{
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 80vh;
    padding: 20px;
}
#profile-top{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  flex-wrap: wrap;
    align-items:center;
}

#input-data{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 10px;
}



#rewards-menu{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding:20px;
    align-items: center;
}

#coins{
    font-size: 60px;
    color:#40a9ff;
    margin:10px;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.coins-logo-gold{
   
    color: 	#FFD700;
}

#main{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

#refer-container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding:20px;
    align-items: center;
}

#redeem-container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding:20px;
    align-items: center;
}
#main-container{
    flex-direction: column;
    flex: 1 1;
}

